import React from "react"
import Navbar from "react-bootstrap/Navbar"
import Nav from "react-bootstrap/Nav"
import NavDropdown from "react-bootstrap/NavDropdown"
import { Link } from "gatsby"

function NavigationBar() {
  return (
    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
      {/* <Navbar.Brand  as={Link} to="/">Home</Navbar.Brand> */}
      <Nav className="justify-content-left">
        <Nav.Link
          className="ml-2 justify-content-end"
          as={Link}
          to="/"
          title="About"
        >
          Home
        </Nav.Link>
      </Nav>

      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse
        id="responsive-navbar-nav"
        className="justify-content-end"
      >
        <Nav className="justify-content-end">
          <Nav.Link className="ml-2" as={Link} to="/blog" title="Blog">
            Blog
          </Nav.Link>
          <Nav.Link className="ml-2" as={Link} to="/projects" title="Projects">
            Projects
          </Nav.Link>
          <NavDropdown title="My Lists" id="collapsible-nav-dropdown" className="pl-2 text-left">
            <NavDropdown.Item className="ml-2" as={Link} to="/about">Birds List</NavDropdown.Item>
            <NavDropdown.Item className="ml-2" as={Link} to="/about">Books List</NavDropdown.Item>
            <NavDropdown.Item className="ml-2" as={Link} to="/about">Movies List</NavDropdown.Item>
          </NavDropdown>
          <Nav.Link className="ml-2" as={Link} to="/about" title="About">
            About Me
          </Nav.Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}

export default NavigationBar
